// src/CodePost.js
import React from "react";
import { useParams } from "react-router-dom";

const CodePost = ({ posts, isBlogView }) => {
	const { postId } = useParams();
	const post = posts.find((p) => p.id === parseInt(postId));

	if (!post) {
		return <div>    .</div>;
	}

	// Blog View   (MDN )
	if (isBlogView) {
		return (
			<div className="max-w-3xl mx-auto p-6 bg-white shadow-md rounded-lg">
				<h1 className="text-4xl font-bold text-gray-900 mb-2">
					{post.title}
				</h1>
				<h2 className="text-2xl text-gray-600 mb-4">{post.subtitle}</h2>
				<p className="text-gray-700 leading-relaxed mb-8">
					{post.content}
				</p>

				<h3 className="text-2xl font-bold text-gray-900 mt-8 mb-4">
					 
				</h3>
				<ul className="list-disc list-inside text-gray-700 space-y-2 mb-8">
					<li>React Virtual DOM </li>
					<li>React   Context API</li>
					<li>Hooks : useEffect useReducer</li>
				</ul>

				<h3 className="text-2xl font-bold text-gray-900 mt-8 mb-4">
					  
				</h3>
				<ul className="list-disc list-inside text-gray-700 space-y-2">
					<li>JavaScript  </li>
					<li>ES6   JavaScript </li>
					<li>React  JSX </li>
				</ul>
			</div>
		);
	}

	// References View   (  Live Preview )
	return (
		<div className="max-w-3xl mx-auto p-6 bg-white shadow-md rounded-lg">
			<h1 className="text-4xl font-bold text-gray-900 mb-6">
				{post.title}
			</h1>

			{/*   */}
			<div className="mb-6 p-4 bg-yellow-100 border-l-4 border-yellow-500 rounded shadow-inner text-gray-800">
				<p className="text-lg font-medium mb-2">:</p>
				<p className="leading-relaxed">{post.memo}</p>
			</div>

			{/*   */}
			<div className="mb-6 relative">
				<pre className="text-gray-700 bg-gray-100 p-4 rounded-lg overflow-auto">
					{post.content}
				</pre>
			</div>

			{/* Live Preview  */}
			<div className="mb-4 border-t pt-4">
				<h3 className="text-xl font-semibold mb-2">Live Preview</h3>
				<iframe
					title="Live Preview"
					srcDoc={post.content}
					className="w-full h-96 border rounded"
				></iframe>
			</div>
		</div>
	);
};

export default CodePost;
